body {
  margin: 0;
  padding: 0;
  font-family: Muli;
}

.navbar-nav>li, .navbar-nav {
  float: left;
}

.navbar-nav.navbar-right:last-child {
  margin-right: -15px;
}

.navbar-right {
  float: right;
}

.nav {
  background-color: #00A;
  box-shadow: 2px 2px 4px rgb(77, 77, 77);
}

.nav-img {
  color: #ffffff;
  margin-right: 1rem;
}

.navbar {
  padding: 1rem;
} 

.nav-item {
  font-size: large;
  font-weight: 400;
  margin-right: 0.5rem;
  margin-left: 0.5rem;
}

.navbar-light .navbar-nav .nav-link {
  color: #ffffff;
}

.navbar-light .navbar-brand{
  color: #ffffff;
}

.navbar-light .navbar-toggler {
  background-color: #ffffff;
}

.dropdown-item {
  font-size: 20px;
}

.main-content {
  margin-top: 5.5rem;
  text-align: center ;
}

.main-desc {
  margin-left: auto;
  margin-right: auto;
  margin-top: 2rem;
  width: 350px;
  text-align: justify;
  line-height: 35px;
}

.main-img {
  margin-top: 3.5rem;
  margin-bottom: 3.5rem;
  width: 250px;
  background-color: #00A;
  border-radius: 50%;
}

.in-touch {
  justify-content: center;
}

.icons {
  text-align: center;
}

.fa-icon {
  margin-top: 5rem;
  margin-left: 2.5rem;
  margin-right: 2.5rem;
  justify-content: space-around;
}

@media (max-width: 820px) {
  .personal-links {
      display: inline-block;
  }
}

.body-content {
  margin-top: 1.5rem;
  padding: 5rem;
  color: #ffffff;
  background-color: #00A;
  text-align: center;
}

.btn {
   margin: 1.5rem;
   width: 100%;
   display: inline-block;
   color: #ffffff;
   border: #ffffff;
}

.footer {
  text-align: center;
  margin-top: 3rem;
  margin-bottom: 1.5rem;
}